.profile-view {
    border-right: 1px solid rgba(0, 0, 0, .075);
    width: 100%;
    padding-top: 1.7rem;
}

.banner-img {
    width: 100%;
    height: 200px;
    object-fit: cover;
}


.profile-container {
    width: 100%;
    position: relative;
    text-decoration: none;
}

.profile__picture {
    margin-left: 1em;
    width: 100;
}

.profile__picture__shell {
    background-color: white;
}

.profile__large {
    padding: 3.35em;
    position: relative;
    top: -15px;
    border: 3px solid rgba(255, 255, 255, 1);
    outline-style: none;
    width: 100%;
}

.profile__info {
    padding-top: 2.4rem;
    margin-left: 1rem;
    display: inline-block;
}

.follower-following-display {
    display: flex;
}

.following-count, .follower-count {
    display: flex;
}

.number {
    margin-bottom: 0 !important;
    margin-right: .4em;
    font-weight: bold;
}

.text {
    margin-bottom: 0;
    margin-right: .6em;
}

.profile__buttons {
    position: relative;
    right: -450px;
    top: -200px;
}

.profile-link {
    color: black;
    text-decoration: none;
}

.profile-link:hover {
    color: black;
}

.profile__action-button {
    border-radius: 20px;
    padding: .33em .55em;
    border: 1px solid rgba(0, 0, 0, .2);
    background-color: white;
}

.user-display__container {
    color: black;
    text-decoration: none;
}

.profile__action-button:hover {
    background-color: rgba(0, 0, 0, .075);
}





